<template lang="pug">
  .nav-container.mobile(:class="{expanded: menuExpanded}")
    nav.main-nav
      li: a(href="#", @click.prevet="goTo('')").nav-button Lobby
      li: a(href="#", @click.prevet="goTo('clientes')").nav-button Clientes
      li: a(href="#", @click.prevet="goTo('servicios')").nav-button Servicios
      li: a(href="#", @click.prevet="goTo('contratos')").nav-button Contratos
      li: a(href="#", @click.prevet="goTo('extras')").nav-button Extras
      li: a(href="#", @click.prevet="goTo('secciones')").nav-button Secciones
      li: a(href="#", @click.prevet="goTo('reportes')").nav-button Reportes
</template>

<script>
  export default {
    name: 'HomeHeader',
    data() {
      return {
        menuExpanded: false,
      };
    },

    mounted() {
      const self = this;
      window.appBus.$on('toggle-menu', () => {
        self.changeMenu();
      });
    },

    methods: {
      changeMenu() {
        this.expandMenu();
        if (!this.menuExpanded) {
          this.menuExpanded = true;
        } else {
          setTimeout(() => {
            this.menuExpanded = false;
          }, 600);
        }
      },

      expandMenu() {
        const icon = this.menuExpanded ? 'menu' : 'clear';
        if (this.menuExpanded) {
          this.hideMenu();
        } else {
          this.showMenu();
        }
        this.animateCss('.hamburger-menu', 'rotateIn', icon);
      },

      showMenu() {
        this.animateCss('.nav-container', 'bounceInRight');
      },

      hideMenu() {
        this.animateCss('.nav-container', 'bounceOutRight');
      },

      animateCss(element, transitionName, icon) {
        const theClass = `animated ${transitionName}`;
        $(element)
          .removeClass(theClass)
          .addClass(theClass)
          .one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', (e) => {
            const $this = $(e.target);
            $this.removeClass(theClass);
            if (icon) $this.find('i').text(icon);
          });
      },

      goTo(endpoint) {
        this.changeMenu();
        window.location.href = `${baseURL}app/admin/${endpoint}`;
      }
    }
  };
</script>


<style lang="sass" scoped>
  @import  './../../../assets/css/1-base/_colors.sass'
  @import  './../../../assets/css/1-base/_vars.sass'

  $dark-color: #000
  .nav-container.mobile,
  .hamburger-menu
    display: flex
    justify-self: flex-end
    overflow: hidden
    align-self: flex-end
    margin-left: auto
    align-items: center
    display: none

  .hamburger-menu
    display: none

  .main-nav
    display: table
    color: $primary-color
    list-style: none
    height: 100%
    li
      display: table-cell
      padding: 0 8px
  .nav-button
    color: lighten($dark-color, 10%)
    display: block
    padding:
      top: 5px
      bottom: 5px
    font:
      size: 16px
      weight: bolder
    text-decoration: none
    transition: all ease .5s
    position: relative
    &:after
      content: ''
      position: absolute
      height: 3px
      width: 0
      bottom: 3px
      left: 0
      background: $contrast-color
      border-radius: 3px
      transition: all ease .5s
    &:hover
      text-decoration: none
      color: $contrast-color
      &:after
        width: 100%

  .special
    background: $primary-color
    color: #fff
    width: 150px
    border-radius: 3px
    text-align: center
    position: relative
    &:after
      content: ''
      position: absolute
      height: 100%
      width: 0
      top: 0
      left: 0
      background: transparentize(#fff,.87)
      border-radius: 3px
      transition: all ease .2s
    &:hover
      color: #fff
      &:after
        width: 100%
  @media (max-width: 960px)
    .nav-container.mobile
      display: none
      &.expanded
        display: block
        position: absolute
        width: 100%
        height: 100vh
        top: 70px
        right: 0
        z-index: 101
        background: white
        display: flex
        flex-direction: column
        align-content: center
        .main-nav
          display: block
          height: fit-content
          margin-top: 10%
          width: 100%
          li
            display: flex
            padding: 2% 20%
            text-align: center
            justify-content: center
        .nav-button
          font-size: 22px
    .hamburger-menu
      display: block
      font:
        size: 20px
        weight: 900
    .name
      font-size: 18px
    .statement
      font-size: 7px
</style>




